body {
  transition: background-color 1s;
}

#quote-box {
  display: grid;
  height: 40vh;
  width: 30vw;
  background-color: white;
  margin-left: 35vw;
  margin-top: 30vh;
  font-size: 2em;
  grid-template-areas: 
    "leftQuote text text text rightQuote"
    "author author author author author"
    "twitter twitter twitter newQuote newQuote";
  grid-template-rows: 2fr 1fr 1fr;
  grid-template-columns: 12.5%, 25%, 25%, 25%, 12.5%;
  grid-gap: 1vw;
}

#quote1 {
  grid-area: leftQuote;
  position: relative;
  top: 10%;
  left: 30%;
  transition: opacity 1s;
}
#text {
  grid-area: text;
  word-wrap: normal;
  float: top;
  font-weight: bolder; 
  width: 100%;
  transition: opacity 1s;
}
#quote2 {
  grid-area: rightQuote;
  position: relative;
  top: 10%;
  right: 30%;
  transition: opacity 1s;
}
#author {
  grid-area: author;
  justify-self: center;
  font-style: italic;
  font-weight: lighter;
  transition: opacity 1s;
}
#tweet-quote {
  grid-area: twitter;
}
#tweet-button {
  font-size: 1em;
  text-align: center;
  color: white;
  margin-left: 5%;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  transition: opacity 1s;
}
#new-quote {
  grid-area: newQuote;
  width: 300%;
  height: 5vh;
  margin-left: -250%;
  border: none;
  cursor: pointer;
  border-radius: 5px;
  color: white;
  font-weight: bolder;
  transition: opacity 1s;
}

